import React, { PropTypes } from 'react';
import { Link } from 'react-router';
// import FacebookLogin from 'react-facebook-login';
import { Form2, Title4Top, Separator, Input, Button3, ErrorText } from '../styledComponents';


class LoginForm extends React.Component {
  constructor() {
    super();
    this.state = {};
  }

  render() {
    return (
      <Form2>
        {/* <Separator />
        <FacebookLogin
          appId="258420877937846"
          autoLoad
          fields="name,email,picture"
          onClick={(...args) => console.log('ComponentClick', args)}
          callback={(...args) => console.log(args)}
          version="2.8"
          language="fr"
          textButton="Se connecter avec Facebook"
        /> */}
        <Separator />
        <Title4Top>Déjà client ?</Title4Top>
        <Input
          type="email" placeholder="Votre adresse email"
          onChange={e => this.setState({ email: e.target.value })}
        />
        <Input
          type="password" placeholder="Votre mot de passe"
          onChange={e => this.setState({ password: e.target.value })}
        />
        {this.props.isLoginLoading ?
          <p>chargement</p> :
          <Button3 type="button" onClick={() => this.props.login(this.state.email, this.state.password)}>
            Se connecter
          </Button3>
        }
        {this.props.errorLogin && <ErrorText>Email/mot de passe incorrect(s)</ErrorText>}
        <Separator />
        <Title4Top>Pas encore de compte ?</Title4Top>
        <Link to={'/order/step6'}>
          <Button3 type="button">
            {"S'inscrire"}
          </Button3>
        </Link>
        <Separator />
      </Form2>
    );
  }
}

LoginForm.propTypes = {
  login: PropTypes.func.isRequired,
  errorLogin: PropTypes.bool.isRequired,
  isLoginLoading: PropTypes.bool.isRequired,
};

export default LoginForm;



