import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { browserHistory } from 'react-router';
import Step5 from '../../components/Step5';
import callApi from '../../services/api';
import { trackEvent } from '../../services/analytics';

class Step5Container extends Component {
  constructor() {
    super();
    this.state = {};
    this.login = this.login.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    if (window.location.hostname !== 'localhost') {
      window.onbeforeunload = function () {
        return true;
      };
    }
  }

  login(email, password) {
    this.setState({ isLoginLoading: true });
    callApi('login', 'post', { email, password }).then((res) => {
      this.setState({ isLoginLoading: false });
      this.props.dispatch({ type: 'CUSTOMER.SET_CUSTOMER', customer: res.customer });
      browserHistory.push('/order/step6');
    })
    .catch(() => {
      trackEvent('erreur', 'echec-login');
      this.setState({ errorLogin: true, isLoginLoading: false });
    });
  }

  render() {
    return (
      <Step5
        errorLogin={this.state.errorLogin}
        isLoggued={this.state.isLoggued}
        isLoginLoading={this.state.isLoginLoading}
        login={this.login}
      />
    );
  }
}

Step5Container.propTypes = {
  dispatch: PropTypes.func.isRequired,
};

const mapStateToProps = () => ({});
const mapDispatchToProps = dispatch => ({ dispatch });
export default connect(mapStateToProps, mapDispatchToProps)(Step5Container);



